import clsx from 'clsx'
import React, { FC } from 'react'
import { Typography } from '../../other/Typography'
import { LazyImage } from '../../other/LazyImage'
import { Condition } from '../../other/Condition'
import { BriefCard } from '../BriefCard'
import { AccentVariantProps } from './types'
import './AccentVariant.scss'

const AccentVariant: FC<AccentVariantProps> = ({
  isAccented = false,
  isOutOfStock = false,
  title = '',
  price = 0,
  loading = 'lazy',
  className,
  onClick,
  productColor = '',
  outOfStockTitle = '',
  imgLqip = 'images/productCardThumbnail.png',
  imgSrc = '',
  imgAlt = '',
  shouldOnlyImage = false,
}) => {
  const elementStyle = productColor.startsWith('#')
    ? { background: productColor }
    : {
        background: `center / contain no-repeat url(${productColor})`,
      }

  return (
    <button
      className={clsx(
        className,
        'AccentVariant',
        isAccented && '--accented',
        shouldOnlyImage && '--onlyImage',
        isOutOfStock && '--outOfStock',
      )}
      disabled={isOutOfStock}
      onClick={onClick}
    >
      <Condition match={!isAccented}>
        <BriefCard
          imgAlt={imgAlt}
          imgLqip={imgLqip}
          imgSrc={imgSrc}
          isOutOfStock={isOutOfStock}
          loading={loading}
          outOfStockTitle={outOfStockTitle}
          price={price}
          title={title}
        />{' '}
      </Condition>
      <div className='AccentVariant__ImgWrapper'>
        <LazyImage
          alt={imgAlt}
          className='AccentVariant__Img'
          loading={loading}
          lqip={imgLqip}
          src={imgSrc}
        />
        <div className='AccentVariant__Color' style={elementStyle} />
      </div>
      <Condition match={!shouldOnlyImage}>
        <Typography text={title} type='Caption2Bold' />
      </Condition>
    </button>
  )
}

export default AccentVariant
